.movies{
   
    margin-top: 40px;
    text-align: center;
    
    
    
}
.movies input{
    padding: 10px 30%;
    
}
.movie-poster{
    width: 80%;
    margin: 40px 0 0 120px;
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 30px;
    
}
.movie-poster img{
    width: 250px;
    height: 300px;
    cursor: pointer;
    z-index: 1;
    
}

.poster-data{
    transition: 0.3s;
    margin-bottom: 10px;
    border: 1px solid #151b1f;
    box-shadow: 2px 2px 2px 2px #1c2327;
    transition: opacity 0.4s ease-in-out;
    
}

.poster-data:hover .title{
    z-index: 0;
    opacity: 1;
    
}
.title{
    position: relative;
    bottom: 150px;
    color: white;
    font-size: 14px;
    font-weight: 600;
    z-index: -1;
}
.poster-data:hover img{
    opacity: 0.3;
}

