.heading{
    display: block;
    position: sticky;
    top: 0;
    z-index: 1;
}
.heading-box{
    padding: 30px;
    display: flex;
    background-color: black;
}
.heading-box span{
    color: skyblue;
    font-weight: 700;
    padding-left: 50px;
    cursor: pointer;
}
.heading-box input{
   margin-left: 60%;
   padding-right: 40px;
}